footer {
  position: relative;
  width: 100vw;
  display: flex;
  justify-content: end;
  height: 6em;
}

footer small {
  position: absolute;
  bottom: 3em;
  right: 4em;
}

footer small a {
  color: #455DB2;
}

footer .shadow {
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 12em;
  background: rgb(16,15,22);
  background: linear-gradient(0deg, rgba(16,15,22,1) 0%, rgba(16,15,22,0) 100%);
  z-index: 0;
}