.about-container {
  width: 100vw;
  /* height: 100vh; */
  background-color: rgb(16,15,22);
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #D3D4E7; */
}

.about-card {
  /* background-color: #16191D; */
  background-color: #1b1f24e0;
  width: 90%;
  /* height: 80vh; */
  border-radius: 0.8em;
  padding: 5em 6em;
  margin: 8em 0;
}

.about-card h2 {
  font-weight: 700;
  font-size: 2.6em;
}

.about-card .desc {
  font-weight: 400;
  color: #E4E5EB;
  opacity: 0.6;
  display: flex;
  align-items: center;
  column-gap: 1.6em;
  margin-top: 1em;
}

.about-card .desc .line {
  height: 0.2em;
  width: 2em;
  display: block;
  border-radius: 3em;
  background-color: #455DB2;
}

.about-content {
  margin-top: 3em;
}

.about-content table {
  width: 100%;
  font-size: 0.9em;
}

.about-content table td {
  height: 3em;
  padding: 0 1em;
}

.about-content table tr {
  transition: 0.3s;
}

.about-content table tr:hover {
  background-color: #455DB244;
}

.about-nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.8em;
}

.about-item {
  color: #FFFFFF66;
  font-size: 0.96em;
  transition: 0.2s;
  cursor: pointer;
  border-bottom: 2px solid #1b1f24e0;
  padding-bottom: 0.3em;
}

.about-item:hover {
  color: #455DB2;
}

.about-item.active {
  color: #455DB2;
  font-weight: 600;
  border-bottom: 2px solid #455DB2;
}

.personal-container {
  display: flex;
  column-gap: 2em;
}

.personal-container table {
  width: 100%;
}

.ll-mw {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.ll-mw .title {
  font-weight: 600;
  font-size: 1.2em;
  color: #455DB2;
}

.ll-mw .isi {
  margin-top: 1em;
  font-size: 0.9em;
}

.student-information-container {
  display: flex;
  column-gap: 4em;
  align-items: center;
}

.student-profil {
  width: 10em;
  height: auto;
  border-radius: 0.4em;
  transition: 0.2s;
}

.student-profil:hover {
  transform: scale(1.1);
}

.education-container {
  display: flex;
  flex-direction: column;
  row-gap: 2em;
  position: relative;
}

.education-line {
  position: absolute;
  top: 10px;
  bottom: 3.6em;
  left: 0.5em;
  width: 0.2em;
  background-color: #455DB266;
}

.education-item {
  display: flex;
  column-gap: 2em;
}

.education-item .dot {
  position: relative;
  top: 10px;
  border-radius: 100%;
  background-color: #455DB2;
  width: 1.2em;
  height: 1.2em;
}

.education-item .dot-2 {
  position: relative;
  top: 10px;
  border-radius: 100%;
  width: 1.2em;
  height: 1.2em;
  border: 2px solid #455DB2;
}

.education-item .school {
  font-weight: 600;
}

.education-item .year {
  color: #FFFFFF66;
  font-size: 0.9em;
}

.education-item .major {
  font-size: 0.9em;
}

.experience-container {
  display: flex;
  flex-direction: column;
  row-gap: 2em;
  position: relative;
}

.experience-line {
  position: absolute;
  top: 10px;
  bottom: 10em;
  left: 0.5em;
  width: 0.2em;
  background-color: #455DB266;
}

.experience-item {
  display: flex;
  column-gap: 2em;
}

.experience-item .item {
  width: 50%;
}

.experience-item .gallery {
  flex-shrink: 0;
}

.experience-item .dot {
  position: relative;
  top: 10px;
  border-radius: 100%;
  background-color: #455DB2;
  width: 1.2em;
  height: 1.2em;
  aspect-ratio: 1/1;
}

.experience-item .position {
  /* font-size: 0.9em; */
  font-weight: 600;
}

.experience-item .company {
  /* font-size: 0.9em; */
  margin-bottom: 0.2em;
}

.experience-item .duration, .experience-item .location {
  color: #FFFFFF66;
  font-size: 0.8em;
}

.experience-item .description {
  margin-top: 1em;
  margin-left: 1em;
  font-size: 0.9em;
}

.experience-item .gallery {
  margin-top: 1em;
  margin-left: 1em;
  display: flex;
  justify-content: start;
  align-items: center;
  column-gap: 1em;
}

.experience-item .gallery img {
  width: 10em;
  transition: 0.2s;
  border-radius: 0.4em;
  aspect-ratio: 800 / 565;
}

.experience-item .gallery img:hover {
  transform: scale(1.1);
}

.skill-certification-container {
  display: flex;
  flex-direction: column;
  row-gap: 2em;
}

.skill-certification-container h3 {
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 1em;
  color: #455DB2;
}

.certification-list {
  display: flex;
  flex-direction: column;
  row-gap: 1em;
}

.certification-item {
  display: flex;
  column-gap: 2em;
  padding: 0.4em 1em;
  transition: 0.2s;
  cursor: pointer;
  align-items: start;
}

.certification-item:hover {
  background-color: #455DB244;
}

.certification-item img {
  width: 3em;
  border-radius: 0.4em;
}

.certification-item .class {
  font-weight: 600;
}

.certification-item .company_name {
  color: #FFFFFF66;
  font-size: 0.9em;
}