.contact-container {
  width: 100vw;
  /* height: 100vh; */
  background-image: #16191D;
  position: relative; 
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2em 0;
}

.contact-card {
  width: 80vw;
  padding: 5em 0;
}

.contact-container h2 {
  font-weight: 700;
  font-size: 2.6em;
}

.contact-container .desc {
  font-weight: 400;
  color: #E4E5EB;
  opacity: 0.6;
  display: flex;
  align-items: center;
  column-gap: 1.6em;
  margin-top: 1em;
}

.contact-container .desc .line {
  height: 0.2em;
  width: 2em;
  display: block;
  border-radius: 3em;
  background-color: #455DB2;
}

.container-card {
  position: relative;
  top: -2em;
  margin-bottom: 4em;
}

/* Contact */
.contact-container .card {
  margin-left: 4em;
  width: 40em;
  display: flex;
  flex-direction: column;
  row-gap: 1.8em;
  padding: 3em 4em;
  background-color: #1b1f24e0;
  border-radius: 0.8em;
  font-size: 0.98em;
  position: relative;
  top: 6em;
}

.contact-container .item {
  display: flex;
  flex-direction: row;
  column-gap: 1.4em;
  align-items: center;
}

.contact-container .item i {
  color: #455DB2;
  font-size: 1.2em;
}

.contact-container .item .isi {
  display: flex;
  flex-direction: column;
  row-gap: 0.4em;
}

.contact-container .item .title {
  font-weight: 600;
}

.contact-container .item .value {
  font-size: 0.9em;
  font-weight: 400;
  color: #e4e5eb64;
  transition: 0.2s;
}

.contact-container .item a.value:hover {
  color: #455DB2;
}

.contact-container .item .value i {
  margin-left: 0.8em;
  font-size: 0.8em;
}

.contact-container .items {
  display: flex;
  justify-content: end;
  flex-direction: row;
  column-gap: 1.4em;
  font-size: 1.6em;
}

.contact-container .items a {
  color: #E4E5EB;
}

.contact-container .items i {
  transition: 0.2s;
  cursor: pointer;
}

.contact-container .items i:hover {
  transform: scale(1.2);
}

.tech-container {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  column-gap: 1em;
  padding: 1em 2em;
  border-radius: 0.8em;
  background-color: #E4E5EB;
}

.tech-icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 2.6em;
  height: 2.6em;
  transition: 0.2s;
  cursor: pointer;
}

.tech-icon img {
  width: 100%;
}

.tech-icon:hover {
  transform: scale(1.1);
}

.map {
  position: absolute;
  left: 40em;
  top: 0;
  border-radius: 0.8em;
  z-index: 6 !important;
  width: 36em;
  height: 22em;
}