header {
  width: 100vw;
  /* height: 100vh; */
  position: relative;
}

.header-bg {    
  /* content: ""; */
  background-image: url("../../images/header-bg.jpg");
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.2;
  z-index: -1;
}

.header-shadow {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 12em;
  background: rgb(16,15,22);
  background: linear-gradient(0deg, rgba(16,15,22,0) 0%, rgba(16,15,22,1) 100%);
}

.header-shadow-2 {
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 12em;
  background: rgb(16,15,22);
  background: linear-gradient(0deg, rgba(16,15,22,1) 0%, rgba(16,15,22,0) 100%);
  z-index: 0;
}

.header-content {
  width: 80vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}

.header-text {
  display: flex;
  flex-direction: column;
  row-gap: 0.6em;
  z-index: 10;
}

.header-text h2 {
  font-weight: 600;
  font-size: 1.5em;
  color: #455DB2;
}

.header-text .name {
  font-weight: 700;
  font-size: 3em;
}

.header-text .desc {
  font-weight: 400;
  color: #E4E5EB;
  opacity: 0.6;
  display: flex;
  align-items: center;
  column-gap: 1.6em;
  margin-bottom: 2em;
}

.header-text .desc .line {
  height: 0.2em;
  width: 2em;
  display: block;
  border-radius: 3em;
  background-color: #455DB2;
}

.btn-primary {
  display: inline-block;
  padding: 0.8em 2.2em;
  border: 0.14em solid #455DB2;
  border-radius: 3em;
  font-size: 0.8em;
  color: #E4E5EB;
  background-color: #455DB2;
  transition: 0.2s;
}

.btn-primary:hover {
  background-color: #203379;
  border-color: #203379;
}

.header-image {
  animation: show-image 0.2s linear 0s forwards;
  width: 23em;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1/1;
  border: 0.05em solid #70A5FD80;
  border-radius: 100%;
}

.header-image img {
  width: 88%;
  border-radius: 100%;
  transition: opacity 0.3s ease;
}

.fade-in .header-image img  {
  opacity: 1;
}

.fade-out .header-image img {
  opacity: 0;
}

.circle-container {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.circle {
  position: absolute;
  aspect-ratio: 1/1;
  border: 0.05em solid #70A5FD;
  border-radius: 100%;
  opacity: 0.5;
  z-index: -1;
}

#circle-1 {
  top: -4em;
  left: -4em;
  width: 14em;
}

#circle-2 {
  bottom: -8em;
  left: -8em;
  width: 26em;
}

#circle-3 {
  bottom: -10em;
  right: -10em;
  width: 34em;
}

@media (max-width: 899px) {
  .header-content {
    /* margin-top: 10em; */
    width: 100vw;
    row-gap: 4em;
    flex-direction: column-reverse;
    justify-content: start;
    align-items: center;
  }

  .header-text {
    width: 80%;
  }

  .header-text h2 {
    font-size: 1.2em;
  }
  
  .header-text .name {
    font-size: 2em;
  }
  
  .header-text .desc {
    font-size: 0.8em;
    margin-bottom: 4em;
  }
  
  .header-text .desc .line {
    height: 0.2em;
    width: 1.2em;
  }

  .more {
    display: flex;
    justify-content: center;
  }

  .header-image {
    width: 40%;
    margin-top: 10em;
  }

  #circle-1 {
    top: -4em;
    left: -4em;
    width: 10em;
  }

  #circle-2 {
    bottom: 0em;
    left: -4em;
    width: 10em;
  }
  
  #circle-3 {
    bottom: -3em;
    right: -10em;
    width: 20em;
  }
}