.portfolio-container {
  width: 100vw;
  /* height: 100vh; */
  background-color: #D3D4E7;
  /* padding: 6em; */
  padding: 2em 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.portfolio-container iframe {
  margin: 4em auto 0 auto;
  z-index: 2;
}

.portfolio-card {
  width: 80%;
  padding: 5em 0;
  z-index: 200 !important;
  position: relative;
}

.portfolio-container h2 {
  font-weight: 700;
  font-size: 2.6em;
  color: #16191D;
}

.portfolio-container .desc {
  font-weight: 400;
  color: #16191D;
  opacity: 0.8;
  display: flex;
  align-items: center;
  column-gap: 1.6em;
  margin-top: 1em;
}

.portfolio-container .desc .line {
  height: 0.2em;
  width: 2em;
  display: block;
  border-radius: 3em;
  background-color: #455DB2;
}

.portfolio-list {
  position: relative;
  margin-top: 3em;
  display: flex;
  /* justify-content: space-between; */
  column-gap: 2em;
  flex-wrap: wrap;
  row-gap: 2em;
  z-index: 2;
}

.portfolio-item {
  width: 380px;
  z-index: 2;
}

.portfolio-image {
  width: 100%;
  aspect-ratio: 2/1;
  background-color: #16191D33;
  border-radius: 0.8em;
  overflow: hidden;
  z-index: 2;
}

.portfolio-detail {
  padding: 1em 1.2em;
  color: #16191D;
  z-index: 2;
}

.project-name {
  color: #16191D;
  font-size: 1.1em;
  font-weight: 600;
  z-index: 2;
}

.project-desc {
  margin-top: 1em;
  font-size: 0.9em;
  z-index: 2;
}

.project-tools {
  color: #16191DAA;
  font-size: 0.9em;
}

.project-year {
  color: #16191DAA;
  font-size: 0.9em;
}

.project-link {
  margin-top: 1em;
  font-size: 0.9em;
  display: flex;
  flex-direction: column;
  row-gap: 0.2em;
}

.project-link a {
  transition: 0.2s;
}

.project-link a:hover {
  color: #455DB2;
}

.project-link i {
  margin-left: 0.4em;
  font-size: 0.9em;
  color: #455DB2;
}

.circle-container-portfolio {
  position: absolute;
  z-index: 1 !important;
  width: 100vw;
}

.circle-portfolio {
  position: absolute;
  aspect-ratio: 1/1;
  border: 0.05em solid #16191DAA;
  border-radius: 100%;
  opacity: 0.5;
  z-index: 1 !important;
}

.circle-portfolio-1 {
  top: -4em;
  left: -4em;
  width: 14em;
}

.circle-portfolio-2 {
  top: 20em;
  right: -14em;
  width: 40em;
}

.circle-portfolio-3 {
  bottom: 20em;
  left: -14em;
  width: 37em;
}

.circle-portfolio-4 {
  bottom: -6em;
  right: -8em;
  width: 20em;
}